___CSS_LOADER_EXPORT___.push([module.id, "\n.role .items {\n    color: #212121;\n    border-radius: 8px;\n    border: 1px solid #E3E3E3;\n    overflow: hidden;\n    background-color: #FFFFFF;\n    box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n    margin-top: 8px;\n}\n.role .items .option {\n    color: #212121;\n    padding-left: 12px;\n    line-height: 32px;\n}\n.role .items .option:hover {\n    background-color: #EEEEEE;\n}\n.role i {\n    font-size: 16px;\n    color: #BCBCBC;\n}\n.role-tag-name {\n    align-items: center;\n    padding: 5px 8px;\n    background: #D0D1E9;\n    display: flex;\n    justify-content: center;\n    border-radius: 20px;\n    margin-right: 4px;\n    font-size: 14px;\n    color: #2F3160;\n    white-space: nowrap;\n    font-weight: 500;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/pages/processLivePreview/preDashboard.vue"],"names":[],"mappings":";AAwLA;IACI,cAAc;IACd,kBAAkB;IAClB,yBAAyB;IACzB,gBAAgB;IAChB,yBAAyB;IACzB,0CAA0C;IAC1C,eAAe;AACnB;AAEA;IACI,cAAc;IACd,kBAAkB;IAClB,iBAAiB;AACrB;AAEA;IACI,yBAAyB;AAC7B;AAEA;IACI,eAAe;IACf,cAAc;AAClB;AAEA;IACI,mBAAmB;IACnB,gBAAgB;IAChB,mBAAmB;IACnB,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,iBAAiB;IACjB,eAAe;IACf,cAAc;IACd,mBAAmB;IACnB,gBAAgB;AACpB","sourcesContent":["<script setup lang=\"ts\">\nimport { computed, onBeforeMount, onMounted, ref } from \"vue\";\nimport { container } from \"../../../../domain/core/diContainer\";\nimport { IRouter, routeDefinition } from \"../../../../domain/presentation/IRouter\";\nimport IDEInput from \"../../components/application/IDEInput.vue\";\nimport CustomSelect from \"../../components/customSelect.vue\";\nimport autocomplete from \"../../components/autocomplete.vue\";\nimport { IUser } from \"../../../../../../common/qCloudTemp/runtimeDtoObjects\";\nimport { IIDEInputDefinitions } from \"../../../../domain/presentation/IIDEInputDefinitions\";\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\n\n\n// declare global {\n//     interface Window {\n//         workflow: {\n//             listUsers: () => Promise<Array<LPFlowUser>>;\n//             listUserRoles\n\n\n//         };\n//     }\n// }\n\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\n\ninterface IOptions { name: string, value: string };\ninterface IMultipleOptions { name: string };\n\nconst userOptions = ref<Array<IOptions>>([]);\nconst userRolesOptions = ref<Array<IOptions>>([]);\nconst swimlaneOptions = ref<Array<IOptions>>([]);\n\nconst listUsers = ref<Array<IUser>>([]);\n\nconst usernameInput = ref<string>(\"\");\nconst unitInput = ref<string>(\"\");\nconst rolesInput = ref<Array<string>>([]);\nconst swimlaneInput = ref<Array<string>>([]);\n\nconst unitDisabled = ref<boolean>(false);\nconst rolesDisabled = ref<boolean>(false);\nconst swimlaneDisabled = ref<boolean>(false);\n\nconst validations = ref<Record<\"name\", IIDEInputDefinitions[\"validations\"]>>({\n    name: { minAndMaxLength: { min: 2, max: 40 }, availableCharacters: true, nonSpaceCharacter: true }\n});\nconst isNameValid = ref<boolean>(false);\n\nconst workflow = window.workflow;\n\nonMounted(async () => {\n        executor.execute(\n        async () => {\n            listUsers.value = await workflow.listUsers();\n            convertUserOptions();\n            convertUserRoles();\n            convertSwimlanes();\n        },\n        { loading: true }\n    );\n});\n\nconst isBtnDisabled = computed(()=> !isNameValid || !usernameInput.value || rolesInput.value.length == 0  || swimlaneInput.value.length == 0 );\n\nfunction convertUserRoles() {\n    userRolesOptions.value = workflow.listUserRoles().map((item:string) => { return { name: item, value: item } });\n}\n\nfunction convertSwimlanes() {\n    swimlaneOptions.value = workflow.listSwimlanes().map((item:string) => { return { name: item, value: item } });\n}\n\nfunction convertUserOptions() {\n    let options: Array<IOptions> = [];\n    listUsers.value.forEach((item: IUser) => {\n        options.push({ name: item.name, value: item.id });\n    })\n    userOptions.value = options;\n}\n\nfunction selectUserName(value: string) {\n    usernameInput.value = value;\n    const findUser = listUsers.value.find((item: IUser) => item.id === value);\n    unitDisabled.value = rolesDisabled.value = swimlaneDisabled.value = !!findUser;\n    if (findUser) {\n        unitInput.value = findUser.unit;\n        rolesInput.value = findUser.roles;\n        swimlaneInput.value = findUser.swimlane || [];\n    } \n}\nfunction selectUnit(value: string) {\n    unitInput.value = value;\n}\nfunction selectUserRoles(value: Array<IOptions>) {\n    rolesInput.value = value.map((item: IOptions)=> item.name);\n}\nfunction selectSwimlanes(value: Array<IOptions>) {\n    swimlaneInput.value = value.map((item: IOptions)=> item.name);\n}\n\nasync function continueButton() {\n    executor.execute(\n        async () => {\n            const findUser = listUsers.value.find((item: IUser) => item.id === usernameInput.value)?.name || \"\";\n            const user = { id: usernameInput.value, unit: unitInput.value, name: findUser, roles: rolesInput.value, swimlane: swimlaneInput.value }; //FIXME: define userRequestDTO without id?\n            await workflow.insertUser(user);\n            const router = container.resolve<IRouter>(IRouter);\n            await router.push('../processlivepreview/dashboard');\n        },\n        { loading: true }\n    );\n}\nfunction setNameValidationStatus(isValid: boolean) {\n    isNameValid.value = isValid;\n}\n\n\n</script>\n\n<template>\n    <div class=\"container d-flex justify-content-center align-items-center h-100\">\n        <div class=\"card w-full\">\n            <div class=\"card-header\"> Select or Create User </div>\n            <div class=\"card-body\">\n                <div class=\"row\">\n                    <div class=\"col\">\n                        <autocomplete label=\"Username\" :options=\"userOptions\" @changeValue=\"selectUserName\" />\n                    </div>\n                    <div class=\"col\">\n                        <IDEInput element-id=\"preDashboardUnit\" :isRequired=\"true\" placeholder=\"\" type=\"text\" info-text=\"Unit\" :value=\"unitInput\" @getValue=\"selectUnit\"\n                            :isDisabled=\"unitDisabled\" @validateStatus=\"setNameValidationStatus\" :validations=\"validations.name\" />\n                    </div>\n                </div>\n                <div class=\"row mt-2\">\n                    <div class=\"col\">\n                        <span class=\"select-name\">Roles</span>\n                        <CustomSelect class=\"role\" :options=\"userRolesOptions\" :multiple=\"true\" :disabled=\"rolesDisabled\"\n                            :defaultMultiple=\"rolesInput.map(item => { return { name: item } })\"\n                            multipleSelectedClass=\"role-tag-name\" @input=\"selectUserRoles\" />\n                    </div>\n                    <div class=\"col\">\n                        <span class=\"select-name\">Swimlane</span>\n                        <CustomSelect class=\"role\" :options=\"swimlaneOptions\" :multiple=\"true\" :disabled=\"swimlaneDisabled\"\n                            :defaultMultiple=\"swimlaneInput.map(item => { return { name: item } })\"\n                            multipleSelectedClass=\"role-tag-name\" @input=\"selectSwimlanes\" />\n                    </div>\n                </div>\n            </div>\n            <div class=\"card-footer d-flex justify-content-end\">\n                <button class=\"continue-button\" :style=\"{ opacity: isBtnDisabled ? '0.5': '1' }\" :disabled=\"isBtnDisabled\" @click=\"continueButton\">Continue</button>\n            </div>\n        </div>\n    </div>\n</template>\n<style scoped>\n@import url(\"../../../../styles/IDE3.css\");\n\n.role {\n    width: 100%;\n    font-size: 15px;\n    height: 40px;\n    line-height: 40px;\n    border: 1px solid #BCBCBC;\n    border-radius: 4px;\n    padding: 0px 12px;\n}\n\n.select-name {\n    color: #151233;\n    font-weight: bold;\n    font-size: 14px;\n}\n\n.continue-button {\n    font-weight: 500;\n    font-size: 16px;\n    color: #FFFFFF;\n    background: #151233;\n    padding: 8px 16px;\n    border-radius: 4px;\n    border: none;\n}\n</style>\n<style>\n.role .items {\n    color: #212121;\n    border-radius: 8px;\n    border: 1px solid #E3E3E3;\n    overflow: hidden;\n    background-color: #FFFFFF;\n    box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n    margin-top: 8px;\n}\n\n.role .items .option {\n    color: #212121;\n    padding-left: 12px;\n    line-height: 32px;\n}\n\n.role .items .option:hover {\n    background-color: #EEEEEE;\n}\n\n.role i {\n    font-size: 16px;\n    color: #BCBCBC;\n}\n\n.role-tag-name {\n    align-items: center;\n    padding: 5px 8px;\n    background: #D0D1E9;\n    display: flex;\n    justify-content: center;\n    border-radius: 20px;\n    margin-right: 4px;\n    font-size: 14px;\n    color: #2F3160;\n    white-space: nowrap;\n    font-weight: 500;\n}\n</style>\n\n"],"sourceRoot":""}]);
