___CSS_LOADER_EXPORT___.push([module.id, "\n.autocomplete[data-v-ce7ebd40] {\n    position: relative;\n}\n.autocomplete .label[data-v-ce7ebd40] {\n    margin:0;\n    color: #151233;\n    font-weight: bold;\n    font-size: 14px;\n}\n.autocomplete .icon-arrow[data-v-ce7ebd40] {\n    position: absolute;\n    right: 5px;\n    bottom: 8px;\n    font-size: 16px;\n    color: #A1A1A1;\n}\n.autocomplete ul[data-v-ce7ebd40] {\n    margin: 0;\n    padding: 0;\n}\n.autocomplete li[data-v-ce7ebd40] {\n    list-style: none;\n}\n.autocomplete input[data-v-ce7ebd40] {\n    border-radius: 4px;\n    outline: none;\n    height: 40px;\n    width: 100%;\n    border: 1px solid #A1A1A1;\n    padding-left: 7px;\n    margin-top: 3px;\n}\n.autocomplete .autocomplete-items[data-v-ce7ebd40] {\n    position: absolute;\n    background-color: #FFF;\n    border: 1px solid #A1A1A1;\n    border-radius: 4px;\n    width: -webkit-fill-available;\n    padding: 10px 0;\n    z-index: 9999;\n}\n.autocomplete .autocomplete-items li[data-v-ce7ebd40] {\n    font-size: 14px;\n    line-height: 16px;\n    padding: 8px 12px;\n    color: #212121;\n}\n.autocomplete .autocomplete-items .option[data-v-ce7ebd40] {\n    cursor: pointer;\n}\n.autocomplete .autocomplete-items .option[data-v-ce7ebd40]:hover {\n    background-color: #EEEEEE;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/autocomplete.vue"],"names":[],"mappings":";AA2FA;IACI,kBAAkB;AACtB;AAIA;IACI,QAAQ;IACR,cAAc;IACd,iBAAiB;IACjB,eAAe;AAEnB;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,WAAW;IACX,eAAe;IACf,cAAc;AAClB;AAEA;IACI,SAAS;IACT,UAAU;AACd;AAEA;IACI,gBAAgB;AACpB;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,YAAY;IACZ,WAAW;IACX,yBAAyB;IACzB,iBAAiB;IACjB,eAAe;AACnB;AAEA;IACI,kBAAkB;IAClB,sBAAsB;IACtB,yBAAyB;IACzB,kBAAkB;IAClB,6BAA6B;IAC7B,eAAe;IACf,aAAa;AACjB;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,iBAAiB;IACjB,cAAc;AAClB;AAEA;IACI,eAAe;AACnB;AAEA;IACI,yBAAyB;AAC7B","sourcesContent":["<template>\n    <div class=\"autocomplete\">\n        <div @click=\"openMenu\" @focusout=\"outsideClick\" tabindex=\"0\">\n            <span class=\"label\">{{ props.label }}</span>\n            <input :placeholder=\"placeholder\" :value=\"inputValue\" @input=\"getInputValue\">\n            <span :class=\"['icon-arrow mdi', isItemsOpen ? 'mdi-chevron-up' : 'mdi-chevron-down']\"></span>\n        </div>\n        <div class=\"autocomplete-items\" v-if=\"isItemsOpen\">\n            <ul>\n                <li v-if=\"filteredOptions.length === 0\">No data</li>\n                <li v-for=\"item in filteredOptions\" :key=\"item.value\" class=\"option\" @mousedown=\"selectItem(item)\">{{\n                    item.name }}</li>\n            </ul>\n        </div>\n    </div>\n</template>\n  \n<script setup lang=\"ts\">\nimport { onMounted, ref, watch } from 'vue';\n\n\ninterface IOptions { name: string, value: string | number }\n\nconst emit = defineEmits([\"changeValue\"]);\nconst props = defineProps({\n    options: {\n        type: Array<IOptions>,\n        required: true,\n    },\n    placeholder: {\n        type: String,\n        default: \"\",\n    },\n    value: {\n        type: String,\n        default: \"\"\n    },\n    label: {\n        type: String\n    }\n})\n\nconst inputValue = ref<String>(props.value);\nconst isItemsOpen = ref<boolean>(false);\nconst filteredOptions = ref<Array<IOptions>>([]);\n\nwatch(() => props.options, (_new) => {\n    filteredOptions.value = _new;\n})\n\n// onMounted(() => {\n//     filteredOptions.value = props.options;\n// })\n\nfunction changeInput(e: any){\n    const value = e.target.value;\n    inputValue.value = value;\n    emit('changeValue', value);\n}\nfunction getInputValue(e: any) {\n    const value = e.target.value;\n    inputValue.value = value;\n    filteredOptions.value = props.options.filter(option => option.name.includes(value));\n\n    const findOption = props.options.find(item => item.name === value);\n    if(findOption) {\n        selectItem(findOption);\n    } else {\n        emit('changeValue', value);\n    }\n}\n\nfunction selectItem(item: IOptions) {\n    inputValue.value = item.name;\n    isItemsOpen.value = false;\n    // emit('change', item.value);\n    emit('changeValue', item.value);\n\n}\n\nfunction openMenu() {\n    isItemsOpen.value = !isItemsOpen.value;\n}\n\nfunction outsideClick() {\n    isItemsOpen.value = false;\n}\n\n</script>\n    \n<style scoped>\n.autocomplete {\n    position: relative;\n}\n\n\n\n.autocomplete .label {\n    margin:0;\n    color: #151233;\n    font-weight: bold;\n    font-size: 14px;\n\n}\n\n.autocomplete .icon-arrow {\n    position: absolute;\n    right: 5px;\n    bottom: 8px;\n    font-size: 16px;\n    color: #A1A1A1;\n}\n\n.autocomplete ul {\n    margin: 0;\n    padding: 0;\n}\n\n.autocomplete li {\n    list-style: none;\n}\n\n.autocomplete input {\n    border-radius: 4px;\n    outline: none;\n    height: 40px;\n    width: 100%;\n    border: 1px solid #A1A1A1;\n    padding-left: 7px;\n    margin-top: 3px;\n}\n\n.autocomplete .autocomplete-items {\n    position: absolute;\n    background-color: #FFF;\n    border: 1px solid #A1A1A1;\n    border-radius: 4px;\n    width: -webkit-fill-available;\n    padding: 10px 0;\n    z-index: 9999;\n}\n\n.autocomplete .autocomplete-items li {\n    font-size: 14px;\n    line-height: 16px;\n    padding: 8px 12px;\n    color: #212121;\n}\n\n.autocomplete .autocomplete-items .option {\n    cursor: pointer;\n}\n\n.autocomplete .autocomplete-items .option:hover {\n    background-color: #EEEEEE;\n}\n</style>"],"sourceRoot":""}]);
